<template lang="pug">
//- div #app
//-   img(src="./assets/logo.png")
//-   router-view
.all
  .title 
    h1 台灣各產業經常性薪資研究
  h2 　　這份報告的資料來源，來自政府資料開放平臺-每人每月經常性薪資，探討方向將以薪資漲幅、薪資金額、以及性別來做數據研究，同時也以產業的歷史背景來做推敲。
  .frame
    img(src="./assets/heatmap1.png", alt="")
  h2 從每個產業薪資年增幅做觀察
  p 　　以平均年通膨3%來計算，並用薪資成長幅度百分比來做比較，從民國91年前後，的確可以發現到，幾乎多數的產業，薪資成長幅度已經都跟不上年通膨率了，新聞上常報導的台灣薪資成長停滯了20年，的確是有此現象存在。
  .frame
    img(src="./assets/heatmap2.png", alt="")
  h2 各產業薪資金額變化
  p 　　以經常性薪資金額來看，可以看出前三名最高薪資的產業，分別為電業、金融保險業、傳播資通訊服務業，而近年的處於極度缺工的住宿餐飲業，則是落到最後一名。
  .frame
    img(src="./assets/First.png", alt="")
  h2 先從第一名的電力及燃料業來做觀察，我們可以從台電官網提供的歷史資訊看出脈絡
  p 　　民國75年至79年間，備用容量漸感不足，故本時期除適時興建大型火力、開發優良水力外，乃積極推行時間電價、可停電力、節約用電，並鼓勵汽電共生發電等，力求電力供需平衡，電力系統進入「需求面管理」時期。
  p 　　民國80年代起，電業自由化逐漸蔚為全球風潮。由於國內用電迅速成長，電源開發因地狹人稠而日益艱難，政府乃順應世界潮流，開放民間興建電廠以加速電源開發，台灣發電市場進入「開放發電業」時期。
  p 　　自民國95年起，國際化石燃料價格大漲，嚴重衝擊電業的經營環境。我國自產能源缺乏，為確保電力事業永續發展，在供給面發展低碳電力，在需求面全力推動節約用電及提升用電效率。至此，我國電業市場進入「節能減碳」時期。
  p 　　個人總結，相關電力產業，由於75年後，備載容量不足，代表電力需求已經逐漸上升，爾後80年代電業自由化，所以相關從業人力需求大增，可以看出這段時間的薪資增幅相當驚人，但是從民國96年後，薪資漲幅就接近停滯，從台電提供的資訊來看，很大的原因是因為進口能源成本大漲，所以嚴重壓縮利潤，薪資自然無法在往上調整。
  .frame
    img(src="./assets/second.png", alt="")
  h2 而第二名的金融保險業
  p 　　由於我國保險業自82年市場全面開放後，保險業家數日趨增加，業務競爭亦趨激烈，這時間也正好搭上經濟起飛時期，而我們的保險滲透度，從國際的角度來看也是名列前茅，金融保險從業人員薪資成長快速也是不難理解。
  .frame
    img(src="./assets/third.png", alt="")
  h2 然後則是第三名的傳播資通服務業
  p 　　民國82年，台灣政府開放了電視行業，結束了原先的電視壟斷狀態，引進了私營電視台。這促進了電視節目的多元化和競爭，也為相關行業帶來了更多機會和發展空間，同一時期互聯網也開始大力發展，民國80年到90年，薪資成長相當快速，而90年到100年間就陷入了停滯，個人認為是由於相關技術都已成熟，也沒有更創新的技術出現，自然無法再向上發展，而民國100年後，就遇上了智慧型手機的時代，無線網路的需求大增，可以看出在這之後薪資又開始大幅上揚。
  .frame
    img(src="./assets/last.png", alt="")
  h2 最後則是倒數第一的餐飲服務業
  p 　　從圖表所示可以看出整體薪資金額成長相當緩慢，而台灣的餐飲住宿業成長緩慢可以歸因於以下幾個因素：
  p 1.行業特性：餐飲住宿業通常是勞動密集型行業，工作時間長且辛勤，對於人力成本的壓力較大。此外，該行業的競爭激烈，利潤空間相對有限，因此薪資增長受到經營成本和市場競爭的限制。
  p 2.小型企業主導：台灣的餐飲住宿業以小型企業為主，這些企業往往資金有限、規模較小。小型企業在面對經營壓力時，可能難以提供高薪水。此外，部分小型企業在管理和人力資源管理方面可能較為薄弱，導致薪資增長受到限制。
  p 3.法定最低工資：台灣有法定最低工資的規定，這一規定對於餐飲住宿業尤其重要，因為該行業中有許多低薪職位，例如服務生、清潔人員等。法定最低工資制度可以保障低薪工作者的基本權益，但也限制了薪資增長的空間。
  p 4.人力供需不平衡：台灣的餐飲住宿業普遍存在人力供需不平衡的情況。雖然該行業的需求相對穩定，但因工作環境要求辛苦、薪資待遇不高，吸引人才進入該行業的意願相對較低，導致供應端較為不足。這種人力供需不平衡可能導致雇主對薪資增長的較低需求。
  p 　　總體而言，餐飲住宿業薪資增長緩慢的原因複雜多樣，但政府的政策支持、行業協會的推動、提升服務品質以及員工的培訓和專業發展都是可以改善情況的途徑。這些努力可以提高行業整體的價值和競爭力，進而為員工爭取更好的薪資待遇。
  .frame
    img(src="./assets/maleVSfemale.png", alt="")
  h2 兩性薪資比較
  p 　　從性別的比較來看，這樣直觀來看會覺得大部分的產業幾乎男性的薪資都高於女性，從這方面來看似乎男女有分配不公平的現象，但是從政府提供的經常性薪水資料來看，並沒有包含細部的職業別，所以不能一概而論，但是我們還是可以從這份資料看出一些特別之處，例如醫療保建服務業，差距非常巨大，這很有可能就是男醫生占大多數，以及女護士也偏多有關。再來則是電業開放民營化之後，性別差距突然拉大，一方面可能是因為電力工程多為體力活，以及當時民風都是鼓勵女性當家庭主婦，而在近幾年來看，差距也逐漸縮小，看得出來在女性自主當道的年代，越來越多女性加入這個行業。
  h2 資料來源
