快速，而90年到100年間就陷入了停滯，個人認為是由於相關技術都已成熟，也沒有更創新的技術出現，自然無法再向上發展，而民國100年後，就遇上了智慧型手機的時代，無線網路的需求大增，可以看出在這之後薪資又開始大幅上揚。")]),_c('div',{staticClass:"frame"},[_c('img',{attrs:{"src":require("./assets/last.png"),"alt":""}})]),_c('h2',[_vm._v("最後則是倒數第一的餐飲服務業")]),_c('p',[_vm._v("　　從圖表所示可以看出整體薪資金額成長相當緩慢，而台灣的餐飲住宿業成長緩慢可以歸因於以下幾個因素：")]),_c('p',[_vm._v("1.行業特性：餐飲住宿業通常是勞動密集型行業，工作時間長且辛勤，對於人力成本的壓力較大。此外，該行業的競爭激烈，利潤空間相對有限，因此薪資增長受到經營成本和市場競爭的限制。")]),_c('p',[_vm._v("2.小型企業主導：台灣的餐飲住宿業以小型企業為主，這些企業往往資金有限、規模較小。小型企業在面對經營壓力時，可能難以提供高薪水。此外，部分小型企業在管理和人力資源管理方面可能較為薄弱，導致薪資增長受到限制。")]),_c('p',[_vm._v("3.法定最低工資：台灣有法定最低工資的規定，這一規定對於餐飲住宿業尤其重要，因為該行業中有許多低薪職位，例如服務生、清潔人員等。法定最低工資制度可以保障低薪工作者的基本權益，但也限制了薪資增長的空間。")]),_c('p',[_vm._v("4.人力供需不平衡：台灣的餐飲住宿業普遍存在人力供需不平衡的情況。雖然該行業的需求相對穩定，但因工作環境要求辛苦、薪資待遇不高，吸引人才進入該行業的意願相對較低，導致供應端較為不足。這種人力供需不平衡可能導致雇主對薪資增長的較低需求。")]),_c('p',[_vm._v("　　總體而言，餐飲住宿業薪資增長緩慢的原因複雜多樣，但政府的政策支持、行業協會的推動、提升服務品質以及員工的培訓和專業發展都是可以改善情況的途徑。這些努力可以提高行業整體的價值和競爭力，進而為員工爭取更好的薪資待遇。")]),_c('div',{staticClass:"frame"},[_c('img',{attrs:{"src":require("./assets/maleVSfemale.png"),"alt":""}})]),_c('h2',[_vm._v("兩性薪資比較")]),_c('p',[_vm._v("　　從性別的比較來看，這樣直觀來看會覺得大部分的產業幾乎男性的薪資都高於女性，從這方面來看似乎男女有分配不公平的現象，但是從政府提供的經常性薪水資料來看，並沒有包含細部的職業別，所以不能一概而論，但是我們還是可以從這份資料看出一些特別之處，例如醫療保建服務業，差距非常巨大，這很有可能就是男醫生占大多數，以及女護士也偏多有關。再來則是電業開放民營化之後，性別差距突然拉大，一方面可能是因為電力工程多為體力活，以及當時民風都是鼓勵女性當家庭主婦，而在近幾年來看，差距也逐漸縮小，看得出來在女性自主當道的年代，越來越多女性加入這個行業。")]),_c('h2',[_vm._v("資料來源")]),_c('div',{staticClass:"url"},[_c('a',{attrs:{"href":"https://data.gov.tw/dataset/151337","target":"_blank"}},[_vm._v("政府資料公開平臺")]),_c('a',{attrs:{"href":"https://www.taipower.com.tw/tc/page.aspx?mid=33","target":"_blank"}},[_vm._v("台電-歷史與發展")]),_c('a',{attrs:{"href":"https://history.fsc.gov.tw/History?type=%E4%BF%9D%E9%9A%AA%E5%B1%80","target":"_blank"}},[_vm._v("金管會")])])])}]
